.AbstractList{-webkit-transition:all .4s ease-in-out;-webkit-transition-delay:0s;-moz-transition:all .4s ease-in-out 0s;-o-transition:all .4s ease-in-out 0s;transition:all .4s ease-in-out 0s;padding:1rem 2rem 2rem 2rem}.AbstractList .AbstractList__header{border-bottom:solid 1px rgba(0,0,0,0.1);display:flex;flex-direction:row;font-size:1.1em;font-weight:bold;padding:.5em 0}.AbstractList .AbstractList__header .AbstractList__header-label{display:inline-block}.AbstractList .AbstractList__body{background:#f5f5f5}.AbstractList .AbstractList__body .AbstractList__loading{text-align:center;padding:40px}.AbstractList .AbstractList__body .AbstractList__empty{text-align:center;margin-top:1rem;font-size:1.6rem;background:rgba(255,255,255,0.5);color:rgba(0,0,0,0.3)}.AbstractList .AbstractList__body .AbstractList__empty span{display:inline-block;width:100%;height:100%;padding-top:20px;padding-bottom:20px}.AbstractList .AbstractList__footer{display:flex;justify-content:flex-end;align-items:center;background:#fff;padding:5px;height:auto;border-top:solid 1px rgba(0,0,0,0.1)}.AbstractList .AbstractList__footer .btn-link{color:rgba(0,0,0,0.5)}.AbstractList .AbstractList__footer .btn-link.disabled{opacity:.5}.AbstractList .AbstractList__footer .counter{color:rgba(0,0,0,0.5);font-size:1.1em;margin-bottom:2px}
