.CompanyInfoDisplay{background-color:var(--sys-center-channel-bg);border:1px solid rgba(var(--sys-center-channel-color-rgb), 0.08);box-shadow:0px 2px 3px rgba(0,0,0,0.08);border-radius:4px;color:var(--sys-center-channel-color)}.CompanyInfoDisplay__header{padding:28px 32px 24px 32px;border-bottom:1px solid rgba(var(--sys-center-channel-color-rgb), 0.08);display:flex;align-items:center}.CompanyInfoDisplay__headerText-top{font-weight:600;font-size:16px;line-height:24px}.CompanyInfoDisplay__headerText-bottom{font-size:14px;line-height:20px}.CompanyInfoDisplay__addInfo{margin-left:auto}.CompanyInfoDisplay__addInfoButton{background:var(--sys-button-bg);padding:8px 16px;border-radius:4px;display:flex;color:var(--sys-button-color);align-items:center}.CompanyInfoDisplay__addInfoButton:hover:not(.disabled){background:linear-gradient(0deg, rgba(var(--sys-center-channel-color-rgb), 0.16), rgba(var(--sys-center-channel-color-rgb), 0.16)),var(--sys-button-bg);text-decoration:none;color:var(--sys-button-color)}.CompanyInfoDisplay__addInfoButton:active{background:linear-gradient(0deg, rgba(var(--sys-center-channel-color-rgb), 0.32), rgba(var(--sys-center-channel-color-rgb), 0.32)),var(--sys-button-bg);text-decoration:none;color:var(--sys-button-color)}.CompanyInfoDisplay__addInfoButton:focus{box-shadow:inset 0 0 0 2px var(--sys-sidebar-text-active-border);text-decoration:none;color:var(--sys-button-color)}.CompanyInfoDisplay__addInfoButton.disabled{color:rgba(var(--sys-center-channel-color-rgb), 0.32);background:rgba(var(--sys-center-channel-color-rgb), 0.08)}.CompanyInfoDisplay__addInfoButton>i{font-size:14.4px;line-height:17px}.CompanyInfoDisplay__addInfoButton>i::before{margin:0}.CompanyInfoDisplay__addInfoButton>span{font-weight:600;font-size:12px;line-height:9px;margin-left:4px}.CompanyInfoDisplay__noCompanyInfo{display:flex;flex-direction:column;align-items:center;padding:48px}.CompanyInfoDisplay__noCompanyInfo-message{margin-top:24px;font-size:14px;line-height:20px;color:var(--sys-center-channel-color)}.CompanyInfoDisplay__noCompanyInfo-link{font-weight:600;font-size:14px;line-height:14px;color:var(--sys-button-bg);margin-top:20px;margin-bottom:12px}.CompanyInfoDisplay__companyInfo{padding:28px 45px 30px 32px;color:var(--sys-center-channel-color);font-size:14px;line-height:20px;display:flex}.CompanyInfoDisplay__companyInfo-name{font-weight:600}.CompanyInfoDisplay__companyInfo-addressTitle{font-weight:600;margin-top:16px}.CompanyInfoDisplay__companyInfo-address>div{margin-top:4px}.CompanyInfoDisplay__companyInfo-edit{margin-left:auto}.CompanyInfoDisplay__companyInfo-editButton{color:rgba(var(--sys-center-channel-color-rgb), 0.56);font-size:18px;border-radius:4px;padding:2px}.CompanyInfoDisplay__companyInfo-editButton:hover,.CompanyInfoDisplay__companyInfo-editButton:focus{text-decoration:none;color:rgba(var(--sys-center-channel-color-rgb), 0.72);background:rgba(var(--sys-center-channel-color-rgb), 0.08)}
