.PaymentInfoEdit__card{background-color:var(--sys-center-channel-bg);border:1px solid rgba(var(--sys-center-channel-color-rgb), 0.08);box-shadow:0px 2px 3px rgba(0,0,0,0.08);border-radius:4px;padding:28px 32px;margin-top:20px}.PaymentInfoEdit__paymentForm{padding:0;margin:0;max-width:480px}.PaymentInfoEdit__paymentForm .section-title:not(:first-child){font-size:14px;line-height:20px}.PaymentInfoEdit__paymentForm .section-title{color:var(--sys-center-channel-text)}.PaymentInfoEdit__paymentForm .Input_fieldset{background:var(--sys-center-channel-bg)}.PaymentInfoEdit__paymentForm.Input_fieldset:focus-within{box-shadow:inset 0 0 0 2px var(--sys-button-bg)}.PaymentInfoEdit__paymentForm.Input_fieldset___error{color:var(--sys-error-text);box-shadow:inset 0 0 0 1px var(--sys-error-text)}.PaymentInfoEdit__paymentForm.Input_fieldset___error:focus-within{color:var(--sys-error-text);box-shadow:inset 0 0 0 2px var(--sys-error-text)}.PaymentInfoEdit__paymentForm .Input::placeholder{color:var(--sys-center-channel-color)}.PaymentInfoEdit__error{display:flex;color:var(--sys-error-text);font-size:12px;align-items:center}.PaymentInfoEdit__error i{font-size:14.4px;margin-right:4px}
