.DataGrid{width:100%;padding:2rem 2rem 2rem 2rem;background:#f5f5f5;color:var(--sys-center-channel-color)}.DataGrid .DataGrid_search{height:48px;background:#fff;border-bottom:solid 1px rgba(0,0,0,0.1);display:flex;flex-direction:row;font-weight:bold;padding:.5em 0}.DataGrid .DataGrid_search .DataGrid_searchBar{display:flex;position:relative;height:32px;width:300px;border:1px solid rgba(var(--sys-center-channel-color-rgb), 0.2);box-sizing:border-box;border-radius:4px;margin-left:8px}.DataGrid .DataGrid_search .DataGrid_searchBar input{border:0px;margin-left:32px;margin-right:16px;font-size:.95em;width:100%}.DataGrid .DataGrid_search .DataGrid_searchBar input:focus{outline:none}.DataGrid .DataGrid_search .DataGrid_searchBar .DataGrid_searchIcon{top:5px;left:10px;position:absolute;pointer-events:none;color:rgba(0,0,0,0.5)}.DataGrid .DataGrid_search .DataGrid_searchBar .DataGrid_clearButton{display:flex;align-items:center;margin-right:5px}.DataGrid .DataGrid_header{color:rgba(0,0,0,0.87);border-bottom:solid 1px rgba(0,0,0,0.1);display:flex;flex-direction:row;font-size:1.1em;font-weight:bold;padding:.5em 0}.DataGrid .DataGrid_rows{display:flex;flex-direction:column;background:rgba(var(--sys-button-color-rgb), 0.5)}.DataGrid .DataGrid_rows .DataGrid_loading{margin:auto;font-size:1.6rem;color:rgba(0,0,0,0.3);padding-top:20px;padding-bottom:20px}.DataGrid .DataGrid_rows .DataGrid_empty{font-size:1.6rem;width:100%;padding-top:20px;padding-bottom:20px;color:rgba(0,0,0,0.3);text-align:center}.DataGrid .DataGrid_rows .DataGrid_row{border-left:2px solid transparent}.DataGrid .DataGrid_rows .DataGrid_row:nth-child(odd){background-color:#f5f5f5}.DataGrid .DataGrid_rows .DataGrid_row:hover{background-color:#e0ecfc;border-left:2px solid #166de0}.DataGrid .DataGrid_rows .DataGrid_row .DataGrid_cell{margin:auto;text-overflow:ellipsis;overflow:hidden;white-space:nowrap}.DataGrid .DataGrid_row,.DataGrid .DataGrid_header{display:flex;flex-direction:row;padding-left:18px;height:auto}.DataGrid .DataGrid_row .DataGrid_cell,.DataGrid .DataGrid_header .DataGrid_cell{flex-basis:100%;flex:1 0}.DataGrid .DataGrid_footer{display:flex;flex-direction:row;height:auto;text-align:right;background:#fff;border-top:solid 1px rgba(0,0,0,0.1);border-bottom:solid 1px rgba(0,0,0,0.1);padding:5px 0;color:rgba(0,0,0,0.5);font-size:1.1em}.DataGrid .DataGrid_footer .DataGrid_cell{width:100%;text-align:right}
